<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="cortenip.meta_semana"
    :progress="cortenip.progress"
    :name="cortenip.nombre"
    :depto="cortenip.depto"
    :days="cortenip.dias_laborados"
    :m3="cortenip.m3_persona"
    :bonus_p="cortenip.bono_persona"
    :bonus_tot="cortenip.bono_depto"
    :asis="cortenip.asistencia"
    :extra="cortenip.$_extra_m3"
    :bonus_prod="cortenip.bono_productividad"
    :bonus_goals="cortenip.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Cortenip',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'cortenip',
            userCode: this.$route.params.user,
            city: 'monterrey'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        cortenip() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>